import { NextRequest, NextResponse } from 'next/server';
import { withContactValidation } from '@/lib/middleware/withContactValidation';
import { Resend } from 'resend';

const resend = new Resend(process.env.RESEND_API_KEY);

async function handler(req: NextRequest) {
    const data = await req.json();
    const { name, email, message } = data;
    console.log(`Portfolio <${process.env.FROM_EMAIL}>`)
    try {
        const emailResponse = await resend.emails.send({
            from: `Portfolio <${process.env.FROM_EMAIL}>`,
            to: process.env.TO_EMAIL as string,
            subject: '–ù–æ–≤–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ —Å –ª–µ–Ω–¥–∏–Ω–≥–∞',
            html: `
        <h1>–ù–æ–≤–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ</h1>
        <p><strong>–ò–º—è:</strong> ${name}</p>
        <p><strong>Email:</strong> ${email}</p>
        <p><strong>–°–æ–æ–±—â–µ–Ω–∏–µ:</strong> ${message}</p>
      `,
        });

        if (emailResponse.error) {
            return NextResponse.json(
                { success: false, message: '–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ –ø–∏—Å—å–º–∞' },
                { status: 500 }
            );
        }

        return NextResponse.json(
            { success: true, message: '–ü–∏—Å—å–º–æ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ' },
            { status: 200 }
        );
    } catch  {
        return NextResponse.json(
            { success: false, message: '–û—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞' },
            { status: 500 }
        );
    }
}

export const POST = withContactValidation(handler);
